import axios from 'axios';
import defaultModule from '../components/Module/defaultModule/defaultModule'
import {hashHistory} from 'react-router'
import LoadingFun from '../components/Module_Loading/Loading'
const codeMessage = {
    200: '',
    201: '',
    202: '',
    204: '',
    400: '',
    401: '',
    403: '',
    404: '',
    406: '',
    410: '',
    422: '',
    500: '',
    502: '',
    503: '',
    504: '',
};
function checkStatus(response) {
    if (response.status >= 200 && response.status < 300) {
        return checkDadiCode(response.data)
    }

    const errorText = codeMessage[response.status] || response.statusText;
    const error = new Error(errorText);
    error.name = response.status;
    error.response = response;
    throw error;
}
function checkUrl(project,url) {
    let newProject;
    if (window.location.origin.indexOf('localhost') !== -1 || window.location.origin.indexOf('127.0.0.1') !== -1) {
        newProject = checkProject(project).devUrl
    } else if (window.location.origin.indexOf('test') !== -1) {
        newProject = checkProject(project).testUrl
    } else {
        newProject = checkProject(project).proUrl
    }
    return {url: newProject+url}
}
function checkProject(project) {
    let newProject;
    switch (project) {

        case 'platform-activity-server':
            newProject = {
                testUrl: 'http://jftest.dadi18.com/ddp-web/',
                proUrl: 'yxcat.com.cn/ddp-web/',
                devUrl: 'http://jftest.dadi18.com:8890/platform-activity-server/'
            };
            break;
        case 'platform-sharing-server':
            newProject ={
                testUrl: 'http://jftest.dadi18.com/platform-sharing-server/',
                proUrl: 'http://yxcat.com.cn/platform-sharing-server/',
                devUrl: 'http://localhost:8091/ddp-web/'
            };
            break;
        case 'yxcat-yyh-server':
            newProject ={
                testUrl: 'http://139.224.29.14:8085/yxcat-yyh-server/',
                proUrl: 'http://yxcat.com.cn/yxcat-yyh-server/',
                devUrl: 'http://139.224.29.14:8085/yxcat-yyh-server/'
            };
            break;
        default:
            break;
    }
    return newProject;
}
function checkDadiCode(data) {
    switch (data.code) {
        case "9000":
            hashHistory.push('/');
            break;
        case "0000":
            return data;
            break;
        case "8000":
            defaultModule("",data.message, false,1)
            const errorText = codeMessage[data.message] || "";
            const error = new Error(errorText);
            error.name = data.message;
            error.response = data;
            throw error;
            break;
        default:
            return data;
            break;
    }

}
export default function request(options) {
    const defaultOptions = {
        method: 'post'
    };
    const newOptions = {...defaultOptions, ...options, ...checkUrl(options.project, options.url)};
    if (newOptions.method === "post" || newOptions.method === "get") {
        if (!(newOptions.data instanceof FormData)) {
            newOptions.headers = {
                'Accept': 'application/json',
                'Content-Type': 'application/json; charset=utf-8',
                // 'Content-Type': 'application/x-www-form-urlencoded',
                ...newOptions.headers,
            };
            newOptions.data = JSON.stringify(newOptions.data);
        } else {
            newOptions.headers = {
                'Accept': 'application/json',
                'Content-Type': 'multipart/form-data',
                ...newOptions.headers,
            };
        }
    }
    LoadingFun();
    // console.log(newOptions);
    return axios(newOptions).then(checkStatus).then((response) => {
        LoadingFun(false);
        console.log(response);
        return response
    }).catch(e => {
        LoadingFun(false);
        console.log(e.name,e.response)
    })
}


